import React, { useState } from "react"
import { Card, Button, Alert } from "react-bootstrap"
import { useAuth } from "../contexts/AuthContext"
import { Link, useHistory } from "react-router-dom"

export default function Dashboard() {
  const [error, setError] = useState("")
  const { currentUser, logout } = useAuth()
  const history = useHistory()

  async function handleLogout() {
    setError("")

    try {
      await logout()
      history.push("/login")
    } catch {
      setError("Failed to log out")
    }
  }

  return (
    <>
      <Card>
        <Card.Body>
          <h2 className="text-center mb-4">Profile</h2>
          {error && <Alert variant="danger">{error}</Alert>}
          <strong>Email:</strong> {currentUser.email}
          <Link to="/update-profile" className="btn btn-primary w-100 mt-3">
            Update Profile
          </Link>
          <h1><a href="https://hcboisoppackshub.herokuapp.com/thanks.html">Click Here to view your exclusive limited edition content</a></h1>
         
        <a href="#" class="logo">HC Bois OP Other Pages</a>
        <ul>
            <li><a href= "https://hcboisoppackshub.herokuapp.com/index.html">Home</a></li>
            <li><a href= "https://hcboisoppackshub.herokuapp.com/Downloads.html">Downloads</a></li>
            <li><a href= "https://hcboisoppackshub.herokuapp.com/socials.html">Socials</a></li>
            <li><a href= "https://hcboisoppackshub.herokuapp.com/features.html">Features</a></li>
            <li><a href= "https://hcboisoppackshub.herokuapp.com/ree%20add.html">Requirements/Addons</a></li>
            <li><a href= "#" class="active">Account</a></li>
        </ul>
        </Card.Body>
      </Card>
      <div className="w-100 text-center mt-2">
        <Button variant="link" onClick={handleLogout}>
          Log Out
        </Button>
      </div>
    </>
  )
}
